html, body {
  width: 100vw;
  height: 100vh;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
}

body {
  perspective: 1000px;
  background: url("baba_s_frazoi.79fd958e.jpg") center / cover no-repeat;
  margin: 0;
  font-family: Caveat, cursive;
  overflow: hidden;
}

.dot {
  width: 35px;
  height: 35px;
  background: url("petal.9a23bacd.png") 0 0 / 100% 100%;
  position: absolute;
}

#petals {
  width: 100%;
  height: 100%;
}

.card {
  width: 100%;
  max-width: 800px;
  min-height: 420px;
  z-index: 1;
  transform-style: preserve-3d;
  perspective: 200px;
  opacity: 0;
  background-color: #fff;
  padding: 15px 30px;
  animation: card 2s linear 2s forwards;
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate3d(-40%, -50%, 0)rotateY(15deg);
  box-shadow: 0 0 20px rgba(0, 0, 0, .1);
}

.title {
  background-image: linear-gradient(110deg, #ff6945 0%, #8047ca 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #ff6945;
  text-align: center;
  margin-bottom: 10px;
  font-size: 80px;
  line-height: 1;
}

.text {
  color: #333;
  margin-bottom: 20px;
  padding-bottom: 5px;
  font-size: 35px;
  line-height: 1.2;
}

.author {
  background-image: linear-gradient(110deg, #ff6945 0%, #8047ca 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  opacity: 0;
  color: #121212;
  text-align: right;
  padding-right: 40px;
  font-size: 40px;
  transition: opacity 1s linear;
}

@keyframes card {
  from {
    opacity: 0;
  }

  to {
    opacity: .5;
  }
}

.face {
  width: 80px;
  height: 87px;
  opacity: 0;
  animation: face 2s linear forwards;
  position: absolute;
  top: 42%;
  left: 46%;
}

@keyframes face {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.me {
  animation: me 4s linear 1s forwards;
  position: absolute;
  top: 130%;
  left: 40%;
}

@keyframes me {
  from {
    top: 130%;
  }

  to {
    top: 80%;
  }
}

.heart {
  width: 60px;
  opacity: 0;
  animation: heart 1s linear 5s forwards;
  position: absolute;
